import {SlugEditor, SlugEditorProps} from '../../../ui/slug-editor';
import {useController, useFormContext} from 'react-hook-form';
import {CrupdatePagePayload, useCrupdatePage} from './requests/crupdate-page';
import React, {useEffect, useRef} from 'react';
import clsx from 'clsx';
import {useStickySentinel} from '../../../utils/hooks/sticky-sentinel';
import {useIsMobileMediaQuery} from '../../../utils/hooks/is-mobile-media-query';
import {Button} from '../../../ui/buttons/button';
import {Link, useNavigate} from 'react-router-dom';
import {ArrowBackIcon} from '../../../icons/material/ArrowBack';
import {Trans} from '../../../i18n/trans';
import {HistoryButtons} from '../../../text-editor/menubar/history-buttons';
import {ModeButton} from '../../../text-editor/menubar/mode-button';
import {MenuBar} from './menubar';
import {useTrans} from '../../../i18n/use-trans';
import {toast} from '../../../ui/toast/toast';
import {queryClient} from '../../../http/query-client';
import {Editor} from '@tiptap/react';
import {CustomPage} from '../custom-page';

interface StickyHeaderProps {
  editor: Editor;
  page?: CustomPage;
}
export function StickyHeader({editor, page}: StickyHeaderProps) {
  const {isSticky, sentinelRef} = useStickySentinel();
  const isMobile = useIsMobileMediaQuery();

  return (
    <>
      <div ref={sentinelRef} />
      <div
        className={clsx(
          'sticky top-0 relative z-10 mb-20 bg-paper',
          isSticky && 'shadow'
        )}
      >
        <div className="px-20 py-10 flex items-center justify-between sm:justify-start gap-20 border-b text-muted">
          {!isMobile && (
            <>
              <Button
                variant="text"
                size="sm"
                elementType={Link}
                to="../custom-pages"
                startIcon={<ArrowBackIcon />}
              >
                <Trans message="Back" />
              </Button>
              <FormSlugEditor name="slug" showLinkIcon={false} suffix="pages" />
            </>
          )}
          {editor && <HistoryButtons editor={editor} />}
          {!isMobile && <ModeButton editor={editor} />}
          <SaveButton page={page} editor={editor} />
        </div>
        <MenuBar editor={editor} size="sm" />
      </div>
    </>
  );
}

interface SaveButtonProps {
  page?: CustomPage;
  editor: Editor;
}
function SaveButton({page, editor}: SaveButtonProps) {
  const crupdatePage = useCrupdatePage();
  const navigate = useNavigate();
  const {trans} = useTrans();
  const form = useFormContext();
  const title = form.watch('title');

  return (
    <Button
      variant="flat"
      size="sm"
      color="primary"
      className="min-w-90"
      disabled={crupdatePage.isLoading || !title}
      onClick={() => {
        crupdatePage.mutate(
          {
            pageId: page?.id,
            payload: {
              ...form.getValues(),
              body: editor?.getHTML(),
            },
          },
          {
            onSuccess: () => {
              const msg = page?.id
                ? trans({message: 'Updated page'})
                : trans({message: 'Created page'});
              toast(msg);
              queryClient.invalidateQueries(['custom-pages']);
              navigate('../custom-pages');
            },
          }
        );
      }}
    >
      <Trans message="Save" />
    </Button>
  );
}

interface FormSlugEditorProps extends SlugEditorProps {
  name: string;
}

function FormSlugEditor({name, ...other}: FormSlugEditorProps) {
  const {
    field: {onChange, onBlur, value = '', ref},
  } = useController({
    name,
  });
  const manuallyChanged = useRef(false);

  const {watch, setValue} = useFormContext<CrupdatePagePayload>();

  useEffect(() => {
    const subscription = watch((formVal, {name: fieldName}) => {
      // if user has not changed slug manually, set it based on page title field changes
      if (fieldName === 'title' && !manuallyChanged.current) {
        setValue('slug', formVal.title);
      }
    });
    return () => subscription.unsubscribe();
  }, [watch, setValue]);

  return (
    <SlugEditor
      className={clsx('mr-auto', !value && 'invisible')}
      onChange={e => {
        manuallyChanged.current = true;
        onChange(e);
      }}
      onInputBlur={onBlur}
      value={value}
      inputRef={ref}
      {...other}
    />
  );
}
