import {useMutation} from '@tanstack/react-query';
import {BackendResponse} from '../../../../http/backend-response/backend-response';
import {CustomPage} from '../../custom-page';
import {apiClient} from '../../../../http/query-client';
import {showHttpErrorToast} from '../../../../utils/http/show-http-error-toast';

interface Response extends BackendResponse {
  page: CustomPage;
}

export interface CrupdatePagePayload {
  title?: string;
  body?: string;
  slug?: string;
  hide_nav?: boolean;
}

interface Props {
  payload: CrupdatePagePayload;
  pageId?: number;
}

function CrupdatePage({pageId, payload}: Props): Promise<Response> {
  if (pageId) {
    return apiClient.put(`custom-pages/${pageId}`, payload).then(r => r.data);
  }
  return apiClient.post(`custom-pages`, payload).then(r => r.data);
}

export function useCrupdatePage() {
  return useMutation((props: Props) => CrupdatePage(props), {
    onError: err => showHttpErrorToast(err),
  });
}
